    .titulo-segundo#t_1_2.mt-5
      .h4 1.2 Creacin del Gobierno electrnico
    .p El Gobierno Electrnico es una creacin reciente. Su origen est asociado a los debates en torno a la reforma de las administraciones pblicas durante los aos ochenta y noventa (Menzel, 1998: 445-452).
    .p.text-center #[b Esos debates se alimentaban de tres elementos claros:]
    .row.justify-content-center.align-items-center.mt-5
      .col-9
        img.img-t.img-a(src='@/assets/ilustraciones/img13.svg', alt='') 
    .row.justify-content-center.align-items-center.mt-5
      .col-8       
        .list
          .list__item
              .list__item__bulletNumber.number-1
                  span 1
          .list__txt            
            .h6 Ineficacia de las administraciones pblicas
            .p Deficiencias a la hora de garantizar los derechos de los ciudadanos; la mayora de pases entraban en una fase democrtica con mayor presin sobre el aparato estatal: los ciudadanos demandan ahora eficiencia, eficacia, y economa, pero tambin, transparencia, participacin, equidad, y calidad (Ban y Carrillo, 1997).
            
        .list.my-4
          .list__item
              .list__item__bulletNumber.number-2
                  span 2
          .list__txt            
            .h6 Impulso de la iniciativa privada: Neoliberalismo
            .p El neoliberalismo es la respuesta a la intervencin estatal como garante de una mayor justicia social (es decir, del Estado benefactor).
            p Su enfoque es claro en resultados: privatizaciones, orientacin a la economa de mercado, precios libres, menos subsidios estatales y libertad de mercados.

        .list
          .list__item
              .list__item__bulletNumber.number-3
                  span 3
          .list__txt            
            .h6 Impulso amplio de las TICs
            .p La expansin de tecnologas ofimticas (computadores, software, scanner, impresoras, etc.). Los administradores pblicos son los primeros en recibir el impacto de las tecnologas dentro del Estado.
            p Diversos estudios demuestran que las administraciones pblicas siempre fueron empujadas al uso de las tecnologas para mejorar o subir de estatus sus procesos internos.
            p A la ofimtica se le incorpor el arsenal de las telecomunicaciones del nuevo milenio: mviles, fibra ptica, Wifi, servidores, etc.   
        
        .hr.mt-4
        .p-r.mt-3.text-center MENZEL D., (1998), www.ethics.gov: Issues and Challenges Facing Public Managers, en Public Administration Review, 58, 5, pp. 445-4 
  
    .titulo-segundo#t_1_3.mt-5
      .h4 1.3 Paradigmas que influenciaron la creacin del Gobierno electrnico
    .p El conjunto de los elementos de la leccin anterior se tradujeron en dos grandes enfoques o paradigmas que influenciaron la administracin pblica en las dcadas recientes: el neo-institucionalismo y la Nueva Gestin Pblica (NGP).

    .p De la convergencia de estas corrientes relacionada con la gestin o administracin pblica dara origen al Gobierno electrnico.      

    .row.justify-content-center.align-items-center.mt-5
      .col-9
        img.img-t.img-a(src='@/assets/ilustraciones/img14.svg', alt='') 
    .row.justify-content-center.align-items-center.mt-5
      .col-8       
        .list
          .list__item
              .list__item__bulletNumber.number-1
                  span 1
          .list__txt            
            .h6 Neo-institucionalismo
            .p Segn esta corriente el cambio es resultado de la interaccin entre institucin y entorno. En tanto el ritmo de las transformaciones en el entorno es mucho ms rpido que las transformaciones al interior de las instituciones, se produce una constante tensin entre ambos que obliga a un continuo proceso de adaptacin.
            
        .list.my-4
          .list__item
              .list__item__bulletNumber.number-2
                  span 2
          .list__txt            
            .h6 Gobierno electrnico
            .p Impulso de las TIC para efectos de atender las demandas de los ciudadanos.
            p El trmino e-Gobiemo se consolida a finales de los 90 aunque su origen se remonta a los principios de la historia de los ordenadores (Grnlund, 2005). Mientras que para algunos autores no es ms que otra de las tecnologas de la informacin, refirindose a Internet, que se adopta para el uso del Gobierno (Bretschneder, 2003), para otros es una autntica revolucin.
            p Segn el Gobierno Electrnico es entendido como una de las transformaciones de expresin de la sociedad de la informacin, as como un mbito en el proceso de modernizacin del Estado; el uso estratgico e intensivo de las tecnologas de la informacin y la comunicacin (TIC), tanto en las relaciones del propio sector pblico entre s, como en las relaciones de los rganos del Estado con los ciudadanos, usuarios y empresas del sector privado.


        .list
          .list__item
              .list__item__bulletNumber.number-3
                  span 3
          .list__txt            
            .h6 Nueva Gestin Pblica
            .p El punto central de la NGP es la adopcin de buenas prcticas gerenciales para mejorar la gestin pblica (Barzelay 2001). Para Christopher Hood, uno los mayores analistas de este enfoque en la poca, la NGP sera un trmino para nombrar las doctrinas administrativas ampliamente similares que han dominado la agenda de reforma burocrtica en muchos de los pases desde fnales de la dcada de los aos ochenta (Hood, 1997: 4-5). 

