      .h4 1.4 Gobierno electrnico y sociedad de la informacin    
    .p Con la preparacin y realizacin de las Cumbres Mundiales de la Sociedad de la Informacin (CMSI) -Ginebra, 2003 y Tnez, 2005-, sociedad de la informacin y gobierno electrnico se colocaron como conceptos ligados. En dichas cumbres se seal que el gobierno electrnico es una de las formas de expresin de la sociedad de la informacin y se comprometieron en su implementacin:       
    .row.justify-content-center.align-items-top.mt-5
