    .titulo-segundo#t_1_5.mt-5
      .h4 1.5 Modelos de Gobierno electrnico   
    .p El gobierno electrnico puede definirse como el uso de las TIC para brindar servicios gubernamentales de manera ms efectiva y eficiente a ciudadanos y empresas. Es la aplicacin de las TIC en las operaciones gubernamentales, logrando fines pblicos por medios digitales. El objetivo es mejorar el funcionamiento interno del sector pblico mediante la reduccin de los costos financieros y los tiempos de transaccin para integrar mejor los flujos y procesos de trabajo, con eficencia, buscando soluciones sostenibles. El principio debe respaldarse por un marco institucional de gobierno electrnico eficaz, a travs de la innovacin, transparencia, inclusin y rendicin de cuentas a los ciudadanos y as restaurar la confianza de los ciudadanos en sus gobiernos.         

