        .h6.p-color-1 Gobierno hacia los ciudadanos / consumidores
        p Implica iniciativas diseadas para facilitar la interaccin de las personas con el gobierno como consumidores de servicios pblicos y como ciudadanos.
        p Esto incluye interacciones relacionadas con la prestacin de servicios pblicos, as como con la participacin en el proceso de consulta y toma de decisiones.
        .hr-dot.my-3
        .h6.p-color-2 Gobierno hacia las empresas
        p Incluye transacciones comerciales especficas (por ejemplo, pagos, venta y compra de bienes y servicios), as como la prestacin de servicios centrados en la lnea de negocio.
        .hr-dot.my-3
        .h6.p-color-3 Gobierno hacia otros gobiernos
        p Implica compartir datos y realizar intercambios electrnicos entre actores gubernamentales. Esto implica intercambios intra e interinstitucionales a nivel nacional, as como intercambios entre los niveles nacional, provincial y local.
        .hr-dot.my-3
    .p-r.text-center Fuente: ONU        

    .titulo-segundo#t_1_6.mt-5
      .h4 1.6 Modelo fluctuante de gobierno electrnico
    .p En un enfoque postmoderno aparece la bsqueda por adaptarse a la expansin del mundo con procesos acelerados y no predecibles: una metaestabilidad tal como se conoce en las teoras del Caos y de las Fluctuaciones (Rihani, 2006). Es cclico no hay inicio, no hay final, hay muchos imprevistos y el nivel de integracin del sistema es un prerrequisito y no el objeto.
