      .h4 1.7 Gobierno electrnico en Colombia 
    .p Las Agendas de Conectividad conllevan los principios de la Nueva Gestin Pblica, de la corriente neoliberal y de la Sociedad de la Informacin. Nacen para modernizar la administracin pblica y como proyecto de adaptacin del Estado al cambio global.

    .p Estas Agendas en la regin fueron responsabilidad preferencial de los ministerios de comunicaciones o de las vicepresidencias.
